import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
import empty from "@/components/empty";
import Json from '@/Json';
export default {
	components: {
		uniLoadMore,
		empty
	},
	data() {
		return {
			tabCurrentIndex: 0,
			orderListAll: [],
			goodsFolderName: "goods",
			navList: [{
					orderStatus: 999,
					text: '全部',
					loadingType: 'more',
					orderListAll: []
				},
				{
					orderStatus: 100,
					text: '待付款',
					loadingType: 'more',
					orderListAll: []
				},
				{
					orderStatus: 201,
					text: '待发货',
					loadingType: 'more',
					orderListAll: []
				},
				{
					orderStatus: 301,
					text: '待收货',
					loadingType: 'more',
					orderListAll: []
				},
				{
					orderStatus: 501,
					text: '待评价',
					loadingType: 'more',
					orderListAll: []
				},
				{
					orderStatus: 601,
					text: '售后',
					loadingType: 'more',
					orderListAll: []
				}
			],
		};
	},
	onLoad(options) {
		console.log(options)
		/**
		 * 修复app端点击除全部订单外的按钮进入时不加载数据的问题
		 * 替换onLoad下代码即可
		 */
		// this.tabCurrentIndex = +options.orderStatus;
		if (undefined == options) {
			return;
		}
		for (let i = 0; i < this.navList.length; i++) {
			if (this.navList[i].orderStatus == options.orderStatus) {
				this.tabCurrentIndex = i
				break;
			}
		}



		if (options.orderStatus == 999) {
			this.tabCurrentIndex = 0
			this.loadData()
		}

	},


	methods: {
		//格式化时间
		formatTime(time) {
			if (time == null || time === "") {
				return "";
			}
			return this.formatDate(time);
		},
		//订单支付
		payOrder(item) {
			uni.redirectTo({
				url: '/pages/money/pay?totalMoney=' + item.orderPrice
			})
		},
		//封装查询订单请求参数
		mallOrderModel() {
			let orderModel = new Object();
			orderModel.mallId = this.$base_api.MALL_ID;
			return orderModel;
		},
		//获取订单列表
		loadData(source) {
			//这里是将订单挂载到tab列表下
			console.log("VCBCNCKN")
			let index = this.tabCurrentIndex;
			let navItem = this.navList[index];
			let orderStatus = navItem.orderStatus;

			if (source === 'tabChange' && navItem.loaded === true) {
				//tab切换只有第一次需要加载数据
				return;
			}
			if (navItem.loadingType === 'loading') {
				//防止重复加载
				return;
			}
			if (navItem.loadingType === 'nomore') {
				//防止重复加载
				uni.stopPullDownRefresh()
				return;
			}
			navItem.loadingType = 'loading';
			let orderModel = this.mallOrderModel();
			let isLogin = this.global.isLogin(this);
			if (!isLogin) {
				this.$openPage('Auth');
			}
			this.$minApi.uniapp_post(this.$base_api.queryOrder, JSON.stringify(orderModel), 'json').then(res => {
				var self = this;
				self.orderListAll = res.data;
				if (this.orderListAll != undefined || this.orderListAll.length > 0) {
					for (let a = 0; a < this.orderListAll.length; a++) {
						this.orderListAll[a].orderItemList.forEach(item => {
							item.productPicUrl = this.$base_api.fileReview + '?mallId=' + this.$base_api.MALL_ID + '&folderName=' +
								this.goodsFolderName +
								'&fileName=' + item.productPicUrl;

						})
					}
				}
				setTimeout(() => {
					let orderListAll = this.orderListAll.filter(item => {
						item = Object.assign(item, this.orderStateExp(item.orderStatus));

						if (orderStatus === 999) {
							//0为全部订单
							return item;
						}
						if (item.orderStatus == orderStatus) {
							return item;
						}
						if (orderStatus === 601) {
							if (item.orderStatus == 501 || item.orderStatus == 502) {
								return item;
							}
						}

					});
					orderListAll.forEach(item => {
						navItem.orderListAll.push(item);
					})
					this.$set(navItem, 'loaded', true);
					console.log(this.orderListAll.length + "查看订单" + orderListAll.length);
					//判断是否还有数据， 有改为 more， 没有改为noMore 
					navItem.loadingType = 'nomore';
				}, 600);


			})
		},

		//swiper 切换
		changeTab(index) {
			this.loadData(this.navList[index].orderStatus);
		},
		//顶部tab点击
		tabClick(index) {

			this.tabCurrentIndex = index;
			console.log("tabClick+")
		},
		refound(item) {},
		//确认收货
		receipt(item) {
			uni.showLoading({
				title: '请稍后'
			})
			setTimeout(() => {
				item = Object.assign(item, {
					orderStatus: 501,
				})
				let list = this.navList[3].orderListAll;
				let index =-1;
				for(let i =0;i<list.length;i++){
					if(item.orderId==list[i].orderId){
						index =i
					}
				}
				this.saveOrder(item.orderStatus, item.orderId, "2", "2")
				this.navList[this.tabCurrentIndex].orderListAll.splice(index, 1);
				uni.hideLoading();
			}, 600)

		},
		//删除订单
		deleteOrder(item) {
			uni.showLoading({
				title: '请稍后'
			})
			setTimeout(() => {
				item = Object.assign(item, {
					orderStatus: 102,
				})
				let list = this.navList[0].orderListAll;
				let index =-1;
				for(let i =0;i<list.length;i++){
					if(item.orderId==list[i].orderId){
						index =i
					}
				}
				this.saveOrder(item.orderStatus, item.orderId)
				this.navList[this.tabCurrentIndex].orderListAll.splice(index, 1);
				uni.hideLoading();
			}, 600)
		},
		//评价
		commentOrder(item) {
			// console.log(JSON.stringify(item))
			let orderId = item.orderId
			let url = "/pages/comment/comment?orderId=" + orderId;
			uni.navigateTo({
				url
			})
			if(this.navList[0].orderListAll.length>0){
					this.navList[0].loadingType='more'
					this.navList[0].orderListAll=[]
			}
			if(this.navList[4].orderListAll.length>0){
					this.navList[4].loadingType='more'
					this.navList[4].orderListAll=[]
			}
		},
		//更新订单
		saveOrder(orderStatus, orderId, shippingStatus, payStatus) {
			let orderModel = this.mallOrderModel();
			orderModel.orderStatus = orderStatus
			orderModel.shippingStatus = shippingStatus
			orderModel.payStatus = payStatus
			orderModel.orderId = orderId
			let isLogin = this.global.isLogin(this);
			if (!isLogin) {
				this.$openPage('Auth');
			}
			this.$minApi.uniapp_post(this.$base_api.updateOrder, JSON.stringify(orderModel), 'json').then(res => {
				//更新成功
			})
		},
		//取消订单
		cancelOrder(item) {
			uni.showLoading({
				title: '请稍后'
			})
			console.log(JSON.stringify(item))
			setTimeout(() => {
				let {
					stateTip,
					stateTipColor
				} = this.orderStateExp(101);
				item = Object.assign(item, {
					orderStatus: "101",
					stateTip,
					stateTipColor
				})

				// 取消订单后删除待付款中该项
				let list = this.navList[1].orderListAll;
				let index =-1;
				for(let i =0;i<list.length;i++){
					console.log(list[i].orderId+"^^^^^^^^^")
					if(item.orderId==list[i].orderId){
						index =i
					}
				}
				this.saveOrder(item.orderStatus,item.orderId,"0","0")
				if(this.navList[0].orderListAll.length>0){
						this.navList[0].loadingType='more'
						this.navList[0].orderListAll=[]
				}
				index !== -1 && list.splice(index, 1);
				uni.hideLoading();
			}, 600)
		},

		//订单状态文字和颜色
		orderStateExp(orderStatus) {
			let stateTip = '',
				stateTipColor = '#fa436a';
			switch (+orderStatus) {
				case 100:
					stateTip = '待付款';
					break;
				case 201:
					stateTip = '待发货';
					break;
				case 301:
					stateTip = '待收货';
					break;
				case 501:
					stateTip = '待评价';
					break;
				case 502:
					stateTip = '已评价';
					break;
				case 101:
					stateTip = '订单已关闭';
					stateTipColor = '#909399';
					break;

					//更多自定义
			}
			return {
				stateTip,
				stateTipColor
			};
		}
	},

}
